import '../static/css/todayHeadline.css';
import uniSwipeAction from '@/components/common/uni-swipe-action';
export default {
	data() {
		return {
			hours: new Date().getHours() < 10 ? '0' + new Date().getHours() : new Date().getHours(),
			minutes: new Date().getMinutes() < 10 ? '0' + new Date().getMinutes() : new Date().getMinutes(),
			weekList: ['日','一','二','三','四','五','六'],
			ulList: [],
			opt: [{
				text: '删除',
				style: {
					backgroundColor: 'rgb(255,58,49)'
				}
			}],
		}
	},
	onLoad(options) {
		//动态设置标题
		if (options.label) {
		  uni.setNavigationBarTitle({
			title: options.label
		  })
		}
		uni.setNavigationBarColor({
		  frontColor: '#ffffff',
		  backgroundColor: '#2995f7'
		})
	},
	mounted(){
		//回显数据
		const self = this;
		uni.getStorage({
			key: 'selAlarm',
			success: function (res) {
				const selAlarm = res.data;
				if(selAlarm){
					self.ulList = JSON.parse(selAlarm);
				}
			}
		});
	},
	watch:{
		ulList(newData,oldData){
			uni.setStorage({
				key: 'selAlarm',
				data: JSON.stringify(newData)
			});
		}
	},
	methods:{
		addAlarm(){
			//添加闹钟
			uni.navigateTo({
				url: '/views/pages/setAlarm',
			});
		},
		removeClick(value) {
			this.ulList.splice(value.index,1);
		},
		alarmSwitch(e,index){
			//闹钟开关;
			let obj = this.ulList[index];
			obj['enable'] = e.target.value;
			this.$set(this.ulList,index, obj);
		},
		goEcho(index,item){
			//回显数据
			uni.navigateTo({
				url: '/views/pages/setAlarm?echo=true',
			});
			let obj = item;
			obj['echoIndex'] = index;
			uni.setStorage({
				key: 'echoSelAlarm',
				data: JSON.stringify(obj)
			});
		}
	},
	components:{
		uniSwipeAction
	}
}
